import React, { Component } from 'react';

class Resume extends Component {
  render() {

    if(this.props.data){
    //  var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map(function(education){
        return <div key={education.degree}><h3>{education.degree}</h3>
        <p className="info">{education.school}<span>&bull;</span> <em className="date">{education.timeline}</em></p>
        <p>{education.thesis}</p>
        <p>{education.description}</p></div>
      })
      var work = this.props.data.work.map(function(work){
        return <div key={work.company}><h3>{work.company}</h3>
            <p className="info">{work.title}<span>&bull;</span> <em className="date">{work.years}</em></p>
            <p>{work.description}</p>
        </div>
      })
      var skills = this.props.data.skills.map(function(skills){
      //  var className = 'bar-expand '+skills.name.toLowerCase();
        return <div key={skills.name} className="skillButton"><button className="btn btn-default">{skills.name}</button></div>
      })
      var languages = this.props.data.languages.map(function(languages){
        var className = "bar-expand "+languages.name.toLowerCase();
        return <li key={languages.name}><span style={{width:languages.level}}className={className}></span><em>{languages.name}</em></li>
      })
    }

    return (
      <section id="resume">

      <div className="row education">
         <div className="three columns header-col">
            <h1><span>Education</span></h1>
         </div>

         <div className="nine columns main-col">
            <div className="row item">
               <div className="twelve columns">
                 {education}
               </div>
            </div>
         </div>
      </div>


      <div className="row work">

         <div className="three columns header-col">
            <h1><span>Professional Experience</span></h1>
         </div>

         <div className="nine columns main-col">
          {work}
        </div>
    </div>



      <div className="row skill">

         <div className="three columns header-col">
            <h1><span>Skills</span></h1>
         </div>

         <div className="nine columns main-col">
					  {skills}
			  </div>
      </div>

      <div className="row language">

         <div className="three columns header-col">
            <h1><span>Languages</span></h1>
         </div>

         <div className="nine columns main-col">

				<div className="bars">
				   <ul className="languages">
					  {languages}
					</ul>
				</div>
			</div>
      </div>

   </section>
    );
  }
}

export default Resume;
